import { mapState, mapMutations } from 'vuex';
import {devices} from '@/api/device';
import {listFamilyBindDevices} from '@/api/address';
export default {
	data() {
		return {
			showDialog:false,
			quality:[
				"优","良","轻度污染","中度污染","重度污染","严重污染"
			]
		}
	},
	computed:{
		...mapState(["currentAddress", "devices", "currentUser"])
	},
	onLoad() {
		console.log("onLoad")
		uni.setNavigationBarTitle({
		　　title:'智能硬件'
		})
		
	},
	onHide(){
		console.log("onHide")
	},
	onShow(){
		if(this.currentAddress && this.currentAddress.id){
			listFamilyBindDevices(this.currentAddress.id).then(res=>{
				if(res.data.data && res.data.data.length > 0){
					this.$store.commit("setDevices", res.data.data)
				} else {
					
					
					this.$store.commit("setDevices", [])
				}
			})
		}
		
	},
	mounted(){
		console.log("mounted")  
	},
	created(){
		console.log("created")
	},
	beforeCreate(){
		console.log("beforeCreate")
	},
	beforeMount(){
		console.log("beforeMount")
	},
	destroyed(){
		console.log("destroyed")
	},
	methods: {
		loginCancel(){
			uni.hideLoading();
			this.showDialog = false;
		},
		loginOk(){
			uni.hideLoading();
			this.showDialog = false;
			uni.navigateTo({
				url:'../login/index'
			})
		},
		shortName(name){
			if(name && name.length > 4){
				return name.substr(0, 4) + "...";
			}
			return name;
		},
		...mapMutations(["setSelectDevice", "setDevices"]),
		switchAddress(){
			uni.navigateTo({
				url:'../address/addressList'
			})
		},
		addDevice(){
			uni.navigateTo({
				url:'./addDevice'
			})
		},
		selectDevice(device){
			this.$store.commit("setSelectDevice", device);
			uni.navigateTo({
				url:'./deviceDetail'
			})
		},
		configDevice(device){
			this.$store.commit("setSelectDevice", device);
			uni.navigateTo({
				url:`./configDevice?id=${device.Id}`
			})
		}
	}
}
